body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  margin: 0;
  background-color: white; }

.app__header {
  height: 50px;
  width: 100%;
  background-color: lightblue; }

.app__footer {
  height: 50px;
  width: 100%;
  background-color: lightblue; }

.features__container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin: 10px 10px 0 0; }

.fas {
  color: #2d2d40; }

link {
  cursor: pointer; }

.main__wrapper {
  display: -webkit-flex;
  display: flex;
  margin: 10px 20px; }

.filters__container {
  width: 40vw;
  border: 1px solid black; }

.books__container {
  width: 60vw;
  margin: 0 20px 0 0; }

.books__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }

.books__list-item {
  box-sizing: border-box;
  max-width: 100%;
  height: 230px;
  border: 1px solid #f0f0f0;
  margin-bottom: 20px;
  border-radius: 2px;
  box-shadow: 0px 0px 5px 1px #f0f0f0; }

.fa-trash-alt {
  margin-left: 10px;
  cursor: pointer; }

.book__container {
  margin-left: 20px; }

.book__price {
  width: 80px;
  height: 20px;
  border: 1px solid #f5c454;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 14px;
  background-color: #f5c454;
  color: white; }

.red {
  border: 1px solid #af0d0d;
  background-color: #af0d0d; }

.no-data {
  width: 60vw;
  margin: 0 20px 0 0; }

.app__form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center; }

.form__wrapper {
  margin: 50px auto;
  box-sizing: border-box;
  width: 400px;
  border: 1px solid #f0f0f0;
  border-radius: 2px;
  box-shadow: 0px 0px 5px 1px #f0f0f0;
  padding-bottom: 40px; }

.form__heading {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-transform: uppercase;
  font-size: 20px;
  margin-bottom: 30px;
  text-align: center; }

.form__container {
  box-sizing: border-box;
  outline: none;
  font-size: 18px;
  text-transform: capitalize;
  margin: 0 25px; }

.input {
  box-sizing: border-box;
  outline: none;
  display: block;
  width: 100%;
  padding: 5px;
  border: 1px solid #ddd;
  background: transparent;
  font-size: 14px;
  height: 30px; }

.form__title-container {
  margin-bottom: 10px; }

.form__author-container {
  margin-bottom: 10px; }

.form__price-container {
  margin-bottom: 10px; }

.form__link-submit {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 30px;
  text-decoration: none; }

.form__submit-edit {
  width: 200px;
  height: 30px;
  background-color: #68a3ed;
  border: 1px solid #68a3ed;
  border-radius: 20px;
  cursor: pointer;
  color: white;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
  outline: none; }

.search__title-label {
  display: block; }

.filter__discount-label {
  display: block; }

